body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*posts*/
.container_hw22 {
  display: flex;
  flex-direction: column;
  background-color: rgb(170, 91, 0);
}

.post_hw22 {
  background-color: rgb(4, 46, 4);
  padding: 15px;
  margin: auto;
  width: 700px;
  margin-bottom: 20px;
  padding-top: 10px;
  border-radius: 15px;
}

.header_hw22 {
  display: flex;
}

.header_icon_hw22 {
  padding: 10px;
}

.header_icon_hw22 > img{
  width: 80px;
  height: 80px;
  border-radius: 50%;
  cursor: pointer;
}

.header_content_hw22 {
  width: 100%;
  padding-left: 20px;
  padding-top: 10px;
  position: relative;
  align-content: center;
}

.verified > img {
  max-width: 15px;
  max-height: 15px;
  margin-left: 5px;
}

.name_hw22 {
  font-weight: bold;
  font-size: 22px;
  color: rgb(182, 174, 174);
  cursor: pointer;
}

.header_additional {
  margin-left: 10px;
  font-size: 18px;
  color:#75808a;
}

.content_hw22 {
  font-family: Arial, Helvetica, sans-serif;
  color:#eaeff3;
  font-size: 1.2em;
  width: max-content;
  max-height: max-content;
  position: absolute;
  cursor: pointer;
  margin-top: 15px;
}

.image_hw22 {
  margin-left: 40px;
  margin-right: 10px;
}

.image_hw22 > img{
  border-radius: 40px;
  max-width: 100%;
  cursor: pointer;
}

.icons_hw22 {
  display: flex;
  margin-left: 140px;
  padding-top: 15px;
  color:#8393A0;
  justify-content: space-between;
  padding-right: 12%;
  padding-left: 10px;
}

.footer_icon_hw22 {
  font-size: 32px;
  margin-left: 5px;
  cursor: pointer;

}

.footer_value > img{
  font-size: 24px;
  margin-left: 10px;
  vertical-align: top;
  display: inline-block;
  cursor: pointer;
  max-height: 32px;
  max-width: 32px;
  color:#8393A0;
  -webkit-filter: opacity(1) drop-shadow(0 0 0 #c3ccd3);
  filter: opacity(0.5) drop-shadow(0 0 0 #c2cbd3);
}

/*new post*/
.new_post_container{
  display: flex;
  flex-direction: column;
  background-color: rgb(170, 91, 0);
}

.new_post {
  background-color: rgb(4, 46, 4);
  padding: 15px;
  margin: auto;
  max-width: 700px;
  margin-bottom: 20px;
  padding-top: 10px;
  border-radius: 15px;
  color:#8393A0;
}

.new_post_input {
  display: inline-block;
  width: 100%;
  margin-top: 15px;
  margin-left: 20px;
  text-justify: center;

}

.new_post > h1{
  margin: auto;
  width: 40%;
}

.new_post_input > input{
  margin-left: 10px;
}

.new_post_input > textarea{
  margin-left: 10px;
  width: 350px;
  height: 100px;
  vertical-align: top; 

}

.new_post_button {
  border: none;
  border-radius: 5px;
  padding: 8px;
  margin-top: 20px;
  margin-left: 70%;
  background-color: rgb(122, 187, 17);
  color: black;
  width: 170px;
  font-size: medium;
}

.new_post_button:hover {
  background-color: rgb(183, 226, 114);
  border:1px solid rgb(170, 91, 0)
}